import React from "react";

import { graphql, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';
import Img from 'gatsby-image';

import Layout from "../components/layout_inner";

export default (props) => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "about.jpg" }) {
        childImageSharp {
          fluid(quality: 100, maxWidth: 1920) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | Ольга Емельянцева" defer={false} />
      <h1>
        Обо мне
      </h1>
      <p>&nbsp;</p>
      <div className="row">
        <div>
          <Img fluid={data.file.childImageSharp.fluid} alt="Я" />
        </div>
        <div>
          <p>
            Привет!<br />
            Меня зовут Ольга Емельянцева и я фотограф.
          </p>
          <p>
            Снимаю в городе Минске и Минском районе. Выезжаю в другие города Беларуси.<br />
            Минимальный пакет для выездной съемки - 8 часов, к стоимости пакета добавляются расходы на топливо.
          </p>
          <p>
            Виды фотосъёмок, на которых я специализируюсь:<br />
            Индивидуальная съёмка, Студийная съёмка, Предметная съемка, Love Story, Детская/Семейная съёмка, В ожидании чуда, Свадебная фотосъёмка.<br />
            Всегда на фотосъёмке стараюсь выложиться на все 100%. К работе подхожу ответственно, чтоб добиться желаемого результата. 
            Помогу подготовиться и подскажу как, где, когда и почему.
            Также помогу найти нужных визажистов и парикмахеров.
          </p>
          <p>
            С нетерпением жду работы с Вами в ближайшее время ;) !
          </p>

          <h2 className="small-spacing">
            СОЦИАЛЬНЫЕ СЕТИ
          </h2>
          <p>
            <a className="social-link" href="https://www.instagram.com/olga_emelyantsava" target="_blank" rel="noreferrer" aria-label="instagram"><i className="fab fa-instagram"></i></a>
            <a className="social-link" href="https://vk.com/phot_olio" target="_blank" rel="noreferrer" aria-label="vkontakte"><i className="fab fa-vk"></i></a>
          </p>
        </div>
      </div>
      <div style={{ paddingBottom: "3%" }} className="clear"></div>
    </Layout>
  );
};
